
.logoContainer {
  position: absolute;
  left: 45px;
  top: 15px;
  z-index: 1200;
  display: flex;
  align-items: center;
  padding: 10px;
  .logo {
    width: 24px;
    margin-right: 10px;
  }
  .logoTitle {
    display: inline;
    color: #9e9e9e;
  }
}


.search {
  position: absolute;
  top: 1rem;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  max-width: 400px;
  z-index: 10;
}

.search input {
  padding: 0.5rem;
  font-size: 1rem;
  width: 100%;
}

.popover li {
  padding: 15px 10px;
}

.locate {
  position: absolute;
  z-index: 1000;
  right: 9px;
  bottom: 110px;
}